// pages/admin/approve.js
import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import { toast } from "sonner";

export default function ApproveProviders() {
  const [providers, setProviders] = useState([]);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  // üîí Check admin login
  useEffect(() => {
    const token = localStorage.getItem("adminToken");
    if (!token) {
      toast.error("üîí Please log in as admin", {
        style: { background: "#FFF2E5", color: "#E25822", border: "1px solid #E25822" },
      });
      router.push("/admin/login");
    }
  }, []);

  // ‚úÖ Fetch pending providers
  useEffect(() => {
    async function load() {
      try {
        const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/providers/pending`);
        const data = await res.json();
        setProviders(data.providers || []);
      } catch (err) {
        console.error(err);
        toast.error("‚ùå Failed to load providers.", {
          style: { background: "#FFF2E5", color: "#E25822", border: "1px solid #E25822" },
        });
      } finally {
        setLoading(false);
      }
    }
    load();
  }, []);

  // ‚úÖ Approve provider
  const handleApprove = async (id) => {
    try {
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/providers/approve?id=${id}`, {
        method: "POST",
      });

      const data = await res.json();
      if (!res.ok) throw new Error(data.message || "Approval failed");

      toast.success("‚úÖ Provider approved successfully!", {
        style: { background: "#FFF2E5", color: "#E25822", border: "1px solid #E25822" },
      });

      setProviders((prev) => prev.filter((p) => p.id !== id));
    } catch (err) {
      console.error("‚ùå Approve error:", err);
      toast.error(err.message || "Something went wrong", {
        style: { background: "#FFE4E1", color: "#E25822", border: "1px solid #E25822" },
      });
    }
  };

  // ‚úÖ UI
  return (
    <div className="min-h-screen bg-gradient-to-b from-orange-50 to-white px-8 py-10">
      <h1 className="text-2xl font-semibold text-orange-600 mb-6">Pending Providers</h1>

      {loading ? (
        <p className="text-gray-500">Loading...</p>
      ) : providers.length === 0 ? (
        <p className="text-gray-500">No pending providers üéâ</p>
      ) : (
        <div className="overflow-x-auto bg-white shadow-md rounded-xl p-4 border border-orange-100">
          <table className="min-w-full text-sm">
            <thead>
              <tr className="text-left border-b border-orange-100 text-orange-700">
                <th className="p-3">Name</th>
                <th className="p-3">Phone</th>
                <th className="p-3">Branch</th>
                <th className="p-3">Service</th>
                <th className="p-3">District</th>
                <th className="p-3">Actions</th>
              </tr>
            </thead>
            <tbody>
              {providers.map((p) => (
                <tr key={p.id} className="border-b border-gray-100 hover:bg-orange-50 transition">
                  <td className="p-3 font-medium">{p.name}</td>
                  <td className="p-3">{p.phone}</td>
                  <td className="p-3 text-gray-500">‚Äî</td>
                  <td className="p-3 text-gray-500">‚Äî</td>
                  <td className="p-3 text-gray-500">‚Äî</td>
                  <td className="p-3 flex gap-2">
                    <button
                      onClick={() => handleApprove(p.id)}
                      className="bg-orange-500 hover:bg-orange-600 text-white px-4 py-1 rounded-lg text-sm transition"
                    >
                      Approve
                    </button>
                    <button
                      className="bg-gray-200 hover:bg-gray-300 text-gray-700 px-4 py-1 rounded-lg text-sm transition"
                    >
                      Reject
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}
